.wrapper {
    width: 100%;
    // margin: 0 auto;
}

.inner {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
    position: relative;
}

.banner-wrapper {
    background-color: #0B0E2B;
    width: 100%;
    height:91vh;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0;

    > img {
        width: 100%;
        height: 100%;
        object-fit: cover;
    }
}

.title {
    color: var(--primary);
    margin-top: 80px;
    margin-bottom: 30px;
    font-size: 30px;
    font-weight: 600;
}

.news-wrapper {
    height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: #f0f0f0;
    padding: 0px 208px;
}

.news-list {
    display: flex;
    justify-content: center;
    gap: 30px;
    margin-bottom: 20px;
}

.cooperation-wrapper {
    height: 60vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: #FFFFFF;
    padding: 0px 486px;

    img {
        height: 120px;
    }
}

.cooperation-logos {
    display: flex;
    // margin: 30px;

    .cooperation-logo {
        margin: 30px;
    }
}

.roadmap-wrapper {
    height: 740px;
    display: flex;
    flex-direction: column;
    align-items: center;
    // background-color: #FFF;
    padding: 50px 156px;
    
    > img {
        height: 80%;
        object-fit: fill;
    }
}

.posts-wrapper {
    height: 640px;
    display: flex;
    flex-direction: column;
    background-color: #EEE;
    padding: 0px 286px;
    align-items: center;
}

.posts {
    display: flex;
    align-items: center;
    gap: 20px;
}
